<template>
  <div id="app">
    <div class="wrapper">
      <header class="header">
        <h1>
          <a>
            <img src="./assets/img/logo.png" alt="떠나자 제주" />
          </a>
        </h1>
      </header>
      <section class="content">
        <div class="map_info">
          <ul class="category">
            <li class="on"><a>관광지</a></li>
            <li><a>맛집</a></li>
            <li><a>숙박</a></li>
          </ul>
          <ul class="item">
            <li v-for="(items,i) in items" :key="i">
              <a @click="displayMap(i)" :index = i>
                <img :src="`${items.repPhoto.photoid.imgpath}`" />
                <b>{{ items.title }}</b>
                <p>{{ items.introduction }}</p>
              </a>              
              <p>{{ items.region1cd.label }} > {{ items.region2cd.label }}</p>
              <p>{{ items.alltag }}</p>
              <p>{{ items.latitude }}</p>
              <p>{{ items.longitude }}</p>
              <div>
                <a href="" >
                  <span>좋아요</span>
                  <em>455</em>
                </a>
              </div>
              <button @click="more()">클릭</button>
            </li>
          </ul>
        </div>
        <div class="map_box"  id="map">
        </div>
      </section>
      

    </div>
  </div>
</template>
<script type="text/javascript" src="//dapi.kakao.com/v2/maps/sdk.js?appkey=690cafe0559fae1b6885179a941d08f0"></script>
<script>
import "./assets/css/style.css"
import axios from 'axios';

export default {
  name: 'App',
  data(){
    return {
      items : [],
      MapArray : [],
      index : 0

    }
  },
  mounted(){
    axios.get('http://api.visitjeju.net/vsjApi/contents/searchList?apiKey=upjklivhbhz6cz9n&locale=kr&category=c1&page=1&pageSize=10')
      .then( result => {
        let item_copy = result.data;
        this.items.push(...item_copy.items)

        this.items.splice(9,90)
        //console.log(this.items)
        this.MapArray.push(this.items);
        //console.log(this.items[0].title)

        //console.log(result.data.items[0].address);

      }
    )
    
    //기본 카카오 맵 api
    this.initMap();

    

    
     
  },
  components: {

  },
  methods : {
    initMap() {
      var mapContainer = document.getElementById('map'),
      mapOption = { 
          center: new kakao.maps.LatLng(33.36489602671088, 126.58970398197701),
          level: 9 // 지도의 확대 레벨
      };
      var map = new kakao.maps.Map(mapContainer, mapOption);


      var positions = [
          {
              title: '카카오', 
              latlng: new kakao.maps.LatLng(33.450705, 126.570677)
          },
          {
              title: '생태연못', 
              latlng: new kakao.maps.LatLng(33.450936, 126.569477)
          },
          {
              title: '텃밭', 
              latlng: new kakao.maps.LatLng(33.450879, 126.569940)
          },
          {
              title: '근린공원',
              latlng: new kakao.maps.LatLng(33.451393, 126.570738)
          }
      ];


      // 마커 이미지의 이미지 주소입니다
      var imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png"; 
          
      for (var i = 0; i < positions.length; i ++) {
          
          // 마커 이미지의 이미지 크기 입니다
          var imageSize = new kakao.maps.Size(24, 35); 
          
          // 마커 이미지를 생성합니다    
          var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize); 
          
          // 마커를 생성합니다
          var marker = new kakao.maps.Marker({
              map: map, // 마커를 표시할 지도
              position: positions[i].latlng, // 마커를 표시할 위치
              title : positions[i].title, // 마커의 타이틀, 마커에 마우스를 올리면 타이틀이 표시됩니다
              image : markerImage // 마커 이미지 
          });
      }

      
      //console.log(this.items[0].latitude)

      

    },
    more() {
      
      
    },
    displayMap(i) {
      
      var mapContainer = document.getElementById('map'),
      mapOption = { 
          center: new kakao.maps.LatLng(33.36489602671088, 126.58970398197701),
          level: 9 // 지도의 확대 레벨
      };
      var map = new kakao.maps.Map(mapContainer, mapOption);

      var imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_red.png', // 마커이미지의 주소입니다  ./img/icon_map.png    
          imageSize = new kakao.maps.Size(64, 69), // 마커이미지의 크기입니다
          imageOption = {offset: new kakao.maps.Point(27, 69)}; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.
      
      // 마커의 이미지정보를 가지고 있는 마커이미지를 생성합니다
      var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption),
          markerPosition  = new kakao.maps.LatLng(this.items[i].latitude, this.items[i].longitude); 

      // 마커를 생성합니다
      var marker = new kakao.maps.Marker({
        position: markerPosition,
        image: markerImage // 마커이미지 설정 
      });

      // 마커가 지도 위에 표시되도록 설정합니다
      marker.setMap(map);
      var content  = `
        <div class="map_popup">
          <b>${this.items[i].title}<b> <span>${this.items[i].contentscd.label}</span>
          <p>${this.items[i].address}</p>
          <div>
            <a href="https://map.kakao.com/link/search/${this.items[i].title}" target="_blank" class="btn">카카오맵 보기</a> 
          <div>
        </div>
      `, // 인포윈도우에 표출될 내용으로 HTML 문자열이나 document element가 가능합니다
          position  = new kakao.maps.LatLng(this.items[i].latitude, this.items[i].longitude); //인포윈도우 표시 위치입니다

      // 인포윈도우를 생성합니다
      var customOverlay = new kakao.maps.CustomOverlay({
          map: map,
          position: position,
          content: content,
          yAnchor: 1 
      });
      
    }
  }
}

</script>


